.boton-contenedor{
  height: 83px;
  display: flex;
  flex: 1 1;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 38px;
  background-color: #1b1b32;
  color: white;
  border-radius: 50%;
  margin: 4px;
  cursor: pointer;
  user-select: none;
}

.operador{
  background-color: #61dafb;
  color: #464d5c;
}

.boton-contenedor:hover{
  background-color: #333360;
}

.operador:hover{
  background-color: #81e4ff;
}

.igual{
  background-color: #bd4c00;
}
.igual:hover{
  background-color: #ff6600;
}